html, body {
  height: 100%;
}
*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
input,
button {
  outline: none;
  border: none;
}
a {
  &:link {
    text-decoration: none;
    color: rgba(0, 0, 0, 0.87);
  }

  &:active {
    text-decoration: none;
    color: rgba(0, 0, 0, 0.87);
  }

  &:hover {
    text-decoration: none;
    color: rgba(0, 0, 0, 0.87);
  }

  &:visited {
    text-decoration: none;
    color: rgba(0, 0, 0, 0.87);
  }
}
